@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@300&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@100&family=Roboto+Condensed&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.container{
  font-size: 15px;
  font-family: 'Montserrat', sans-serif;
  padding: 20px;
  margin-top: 20px;
  margin-left: 40px;
}
.card{
  padding: 0px;
  width:25%;
  float: left;
  margin-right: 40px;
  margin-left: 20px;
  box-shadow:10px 10px 15px lightgrey;
}
.header{
  background-color: #ffcc66;
  background-clip: padding-box;
  padding: 10px;
  padding-top: 15px;
  padding-left: 15px;
  padding-bottom: 5px;
}
.header-2{
  background-color: #00b33c;
  background-clip: padding-box;
  padding: 10px;
  padding-top: 15px;
  padding-left: 15px;
  padding-bottom: 5px;
  color: white;
}
.header-3{
  background-color: #00b8e6;
  background-clip: padding-box;
  padding: 10px;
  padding-top: 15px;
  padding-left: 15px;
  padding-bottom: 5px;
  color: #e6ffff;
}
.header-text{
  font-size: 13px;
  padding-left: 10px;
  padding-top: 15px;
}
.card-body{
  padding-left: 0px;
  padding-top: 10px;
}
.list{
  padding-left: 10px;
  padding-right: 15px;
  padding-bottom: 20px;
  height: 180px;
  overflow-y: scroll;
  overflow-x: hidden;
}
.list-item{
  margin-top: 5px;
  margin-bottom: 5px;
  padding-left: 10px;
  padding-top: 5px;
  padding-bottom: 5px;
  background-color:#ffcc66;
  border-radius:3px 3px 25px 3px; 
}
::-webkit-scrollbar{
  width: 0vh;
  display: none;
}
.list-item-2{
  margin-top: 5px;
  margin-bottom: 5px;
  padding-left: 10px;
  padding-top: 5px;
  padding-bottom: 5px;
  background-color:#00b33c;
  color:white;
  border-radius:3px 3px 25px 3px; 
}
.list-item-3{
  margin-top: 5px;
  margin-bottom: 5px;
  padding-left: 10px;
  padding-top: 5px;
  padding-bottom: 5px;
  background-color:#00b8e6;
  color:white;
  border-radius:3px 3px 25px 3px; 
}
.card-footer{
  border-top: 1px solid lightgrey;
  padding-top: 5px;
  padding-bottom: 0px;
  font-weight: bold;
  font-size: 15px;
  background-color: white;
}
.price{
  margin-top: 20px;
  margin-left: 12px;
  padding-left: 30px;
  padding-right: 5px;
  padding-top: 5px;
  padding-bottom: 5px;
  font-size: 20px;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  border-radius: 10px 10px 10px 10px;
  position: relative;
  background-color: #ff5c33;
  color: white;
}
.buy-btn{
  background-image: linear-gradient(to right,#ff944d,#ff6600);
  color: white;
  width: 100%;
  background-clip: padding-box;
  text-align: center;
  padding-top: 15px;
  padding-bottom: 15px;
  font-weight: bolder;
  font-size: 15px;
}
.buy-btn:hover{
  cursor:pointer;
  color: tomato;
  background-image: linear-gradient(to right,white,white);
  border:3px solid tomato;
}
.dot{
  position: absolute;
  width: 10px;
  height: 10px;
  border-radius: 50%;
  margin-left:18px;
  margin-top: 32px; 
  z-index: 100;
  background-color: white;
}
.tag-1{
  position:absolute; 
  padding-left: 2px;
  padding-right: 10px;
  padding-top: 7px;
  margin-top: -15px;
  padding-bottom: 7px;
  margin-bottom: 50px;
  margin-left: 0px;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  border-radius: 3px 3px 3px 3px;
  background-image: linear-gradient(to right,#66ccff,#0077b3);
  color:white;
  font-weight: bolder;
}
.tag-2{
  position:absolute; 
  padding-left: 2px;
  padding-right: 10px;
  padding-top: 7px;
  margin-top: -15px;
  padding-bottom: 7px;
  margin-bottom: 50px;
  margin-left: 0px;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  border-radius: 3px 3px 3px 3px;
  background-image: linear-gradient(to right,#4dff4d,#00b300);
  color:white;
  font-weight: bolder;
}
h1,h2,h3,h4{
  padding: 0px;
  margin: 0px;
}
ul,li{
  list-style: none;
}
a{
  text-decoration: none;
}
button{
  border: 0px;
  background-color: white;
}


